body {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background-image: url(images/wetgif-02-unscreen.gif), url(images/ezgif.com-gif-maker.gif);
    background-repeat: no-repeat;
    background-size: 20%, cover;
    background-position: bottom left, center center;
}





.App input {
    padding: 10px 0px;
    border: 2px solid rgb(88, 4, 4);
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.63);
    position: absolute;
    top: 55%;
    right: 12%;
    transform: translateY(-50%);
    text-align: center;
    font-size: 30px;
    font-style: italic;
    color: rgb(255, 255, 255);
    outline: none;
    animation: moveInput 2s infinite linear alternate;
    text-shadow: 0 0 3px rgb(255, 255, 255);
    width: 23vw;
    box-shadow: 0rem 0rem 1.4rem 1rem rgba(255, 254, 254, 0.6);
}


@keyframes moveInput {
    0% {
        right: 11%;
    }

    100% {
        right: 13%;
    }
}



input::placeholder {
    color: rgb(170, 168, 168);
    transition: 1s;
    text-shadow: none;
}

.beforeContent {
    color: whitesmoke;
    font-size: 30px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    /* margin-top: 50%; */
    text-align: center;
    font-weight: 600;
    animation: showHide 2s linear infinite;
    overflow: hidden;
}

@keyframes showHide {
    0% {
        color: gray;
        top: 0%;
    }

    100% {
        color: whitesmoke;
        top: 80%;
    }
}

.result {
    background-color: rgba(51, 48, 48, 0.616);
    color: rgb(245, 245, 245);
    border: 2px solid rgba(121, 14, 14, 0.904);
    border-radius: 10px;
    text-align: center;
    position: absolute;
    top: 10vh;
    left: 37%;
    transform: translateX(-50%);
    width: 32vw;
    height: 75vh;

}

.snow {
    background-image: url(images/snow.gif);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.sunny {
    background-image: url(images/sunny.gif);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.cloudy {
    background-image: url(images/cloudy.gif);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}


.result h3 {
    color: white;
    margin-bottom: 40px;
    text-align: center;
    font-size: 24px;

}

h3 em {
    color: rgba(252, 124, 124, 0.904);
    font-style: normal;
    font-size: 30px;
    text-shadow: 2px 2px 4px #000000;
}

h4 {
    font-style: italic;
    font-size: 22px;
}

h4 span {
    color: rgba(121, 14, 14, 0.904);
    margin-left: 5px;
    text-shadow: 0 0 13px #FFf;
    animation: shadowChange 2s infinite linear alternate;
    font-size: 22px;
}

@keyframes shadowChange {
    0% {
        text-shadow: 0 0 13px #FFf;
    }

    100% {
        text-shadow: none;
    }
}


.infoBar {
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(31, 31, 30, 0.644);
    height: 4vh;
    width: 100vw;
    font-size: 18px;
}

.infoBar span {
    position: fixed;
    top: 0;
    left: 0;
    border-right: 2px solid rgb(41, 42, 44);
    background-color: rgb(121, 14, 14);
    z-index: 5;
    height: 4vh;
    width: 17vw;
    text-align: center;
    color: rgb(233, 227, 227);
}

.infoBar p {
    position: fixed;
    top: 0vh;
    left: 15vw;
    animation: moveBar 15s infinite linear;
    width: 100vw;
    margin: 0;
    color: whitesmoke;
}

@keyframes moveBar {
    0% {
        left: 100%
    }

    100% {
        left: 0%;
    }
}

.temp {
    font-size: 30px;
    text-align: center;
}

.temp span {
    display: block;
    animation: tempshock 1s infinite linear alternate;
}

@keyframes tempshock {
    0% {
        transform: scale(1.3);
    }

    100% {
        transform: scale(1);
    }

}



@media(orientation: portrait) {
    body {
        background-size: 50%, cover;
        background-position: bottom left -10%, center center;
        overflow: hidden;
    }


    .App input {
        /* padding: 10px 0px; */
        width: 70vw;
        height: 3vh;
        top: 8%;
        left: 50%;
        transform: translateX(-50%);
        font-size: 20px;
        animation: none;
    }

    .beforeContent {
        font-size: 18px;
    }


    .result {
        padding: 0 10px;
        border-radius: 10px 0 0 10px;
        text-align: center;
        position: absolute;
        top: 35vh;
        left: 70%;
        transform: translateX(-50%);
        width: 55vw;
        height: 65vh;
        font-size: 16px;
    }

    .result h3 {
        text-align: center;
        padding: 0 0;
        margin: 0;
        position: fixed;
        top: -15vh;
        left: -40vw;
        /* transform: translateX(-50%); */
        width: 102vw;
        background-color: rgba(51, 48, 48, 0.616);
        display: block;
        font-size: 20px;
    }

    h4 {
        font-size: 16px;
    }

    h4 span {
        display: block;
        margin-left: 2px;
        font-size: 17px;
    }

    .temp {
        font-size: 22px;
        text-align: center;
    }

    .temp span {
        display: block;
        animation: tempshock 1s infinite linear alternate;
        font-size: 26px;
    }

    .infoBar {
        height: 4vh;
        width: 100vw;
        font-size: 15px;
    }

    .infoBar span {
        height: 4vh;
        width: 41vw;
        text-align: center;
        color: rgb(233, 227, 227);
    }

}

@media(orientation: landscape) and (max-width: 430px) {
    .App input {
        width: 90vw;
        right: 50%;
        transform: translateX(50%);
        animation: none;
    }

    .result {
        display: none;
    }

    .infoBar {
        display: none;
    }

}

@media(orientation: landscape) and (max-width: 900px) {
    .App input {
        top: 65%;
        right: 9%;
        font-size: 18px;
        width: 30vw;
        height: 5vh;
        animation: moveInput 1s infinite linear alternate;
    }

    .beforeContent {
        font-size: 18px;
    }

    @keyframes moveInput {
        0% {
            right: 7%;
        }

        100% {
            right: 10%;
        }
    }

    .result {
        text-align: center;
        position: absolute;
        top: 10vh;
        left: 37%;
        transform: translateX(-50%);
        font-size: 16px;
        width: 36vw;
        height: 85vh;
    }

    .result h3 {
        display: none;
    }

    h4 {
        font-size: 16px;
    }

    h4 span {
        font-size: 17px;
        margin-left: 2px;
        margin-bottom: -10px;
    }

    .infoBar {
        height: 6vh;
        font-size: 15px;
    }

    .infoBar span {
        height: 6vh;
        width: 25vw;
    }

    .temp {
        font-size: 18px;
        text-align: center;
    }

    .temp span {
        font-size: 20px;

    }
}